body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-image: url(/pomodoro-clock/static/media/bg-cyber.ce2e6439.jpg);
  min-height: 100vh;
  display: grid;
  grid-template-areas: 
    "title title title"
    ". timer ."
    "footer footer footer";
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: 1fr 500px 1fr;
  grid-template-rows: 250px auto auto auto auto
}
.App-link {
  color: #61dafb;
}
#title{
  grid-area: title;
  width: auto;
  min-width: 300px;
  padding: 25px;
}
#timer{
  grid-area: timer;
  width: 100%;
}
#footer{
  grid-area: footer;
}


#timer{
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-areas:
        'timerSettings'
        'timerDisplay'
        'timerControls'; 
}

#timer-setting{
    grid-area: timerSettings;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-areas: 
        'breakControl sessionControl';
    
}
#break-container{
    grid-area: breakControl;
}
#session-container{
    grid-area: sessionControl;
}

.setting-container{
    display: grid;
    grid-template-areas:
        'label label label'
        'decrement length increment';
    grid-template-columns: 1fr 60px 1fr;

}
.label{
    grid-area: label;
    justify-self: center;
    padding: 10px;
}
.decrementBtn{
    grid-area: decrement;
    justify-self: end;
}
.incrementBtn{
    grid-area: increment;
    justify-self: start;
}
.lengthDisplay{
    grid-area: length;
    justify-self: center;
}

#timer-display{
    grid-area:timerDisplay;
    justify-self: center;
    padding: 15px;
}
.digital-text{
    font-family: 'Orbitron', sans-serif;
    font-size: 1.5em;
    text-align: center;
}
#timer-controls{
    grid-area:timerControls;
    justify-self: center;
    padding-bottom: 15px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
#start_stop{
    grid-area: 1 / 2 / 2 / 3
}
#reset{
    grid-area: 1 / 3 / 2 / 4
}


